import React, { useState } from 'react';
import './Card.css';
import { Button, Carousel, Card as DaisyCard } from 'react-daisyui';
import { priceByValue, sizeByValue, IProduct, approximateSize } from './products';
import { throttle } from './utils';

interface ICardOptions {
  product: IProduct;
  onChange?: (value: number) => void;
}

export function Card(props: ICardOptions) {
  const [value, setValue] = useState(0);
  const [carouselItem, setCarouselItem] = useState(0);
  const hasPromo = typeof props.product.promo !== 'undefined';
  const isUnavailable = props.product.unavailable;

  function handleChange(action: 'inc' | 'dec') {
    const inc = action === 'inc' ? 1 : -1;
    const newValue = value + inc;
    setValue(newValue);
    props?.onChange?.(newValue);
  }
  const incValue = handleChange.bind(null, 'inc');
  const decValue = handleChange.bind(null, 'dec');

  return (
    <DaisyCard className="shadow-sm">
      <figure className="relative">
        <div className="carousel" onScroll={throttle((e: any) => ((e.target as any).scrollLeft === 0 ? setCarouselItem(0) : setCarouselItem(1)), 300)}>
          <Carousel.Item src={props.product.image} alt={props.product.name} className="object-contain w-full h-full" />
          <Carousel.Item style={{ height: 'inherit' }} className="object-contain w-full min-w-full min-h-full overflow-y-scroll inline-block">
            <div className="absolute text-justify text-sm p-1">{props.product.consistsOf}</div>
          </Carousel.Item>
        </div>

        <div className="dots">
          <div className="container">
            <div className={'dot ' + (carouselItem === 0 ? 'active' : '')}></div>
            <div className={'dot ' + (carouselItem === 1 ? 'active' : '')}></div>
          </div>
        </div>
      </figure>

      <DaisyCard.Body className="text-center p-2">
        {hasPromo && <div className="promo">{props?.product?.promo?.name}</div>}
        {isUnavailable && <div className="unavailable">временно недоступно</div>}
        <div className="grow flex flex-col justify-center">
          {props.product.name}
          {typeof props.product.approximateSize !== 'undefined' && <span className="text-xs">{approximateSize(props.product.approximateSize)}</span>}
        </div>
        <div className="flex justify-evenly items-center price">
          {hasPromo && <div className="text-nowrap active">{props.product?.promo?.price}</div>}
          <div className={`text-nowrap ${hasPromo ? 'old' : ''}`}>{props.product.price.display}</div>
        </div>

        <DaisyCard.Actions>
          {value === 0 && (
            <Button color="primary" disabled={isUnavailable} fullWidth={true} onClick={incValue}>
              Добавить
            </Button>
          )}
          {value > 0 && (
            <div className="w-full flex justify-between">
              <Button color="error" shape="square" onClick={decValue}>
                <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 12L18 12" />
                </svg>
              </Button>

              <div className="flex flex-col grow justify-center items-center">
                <div>{sizeByValue(value, props.product)}</div>
                <div>{priceByValue(value, props.product) + '₽'}</div>
              </div>

              <Button color="primary" shape="square" onClick={incValue}>
                <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 6L12 18M6 12L18 12" />
                </svg>
              </Button>
            </div>
          )}
        </DaisyCard.Actions>
      </DaisyCard.Body>
    </DaisyCard>
  );
}
