import React, { useEffect, useState } from 'react';
import './App.css';
import { IOrder, Main } from './Main';
import { Cart } from './Cart';
import { createPurchase, getAddresses, getDeliveryDate, IExportedOrder } from './api';
import { getExportValue, priceByValue, sizeByValue } from './products';

export function App() {
  const [page, setPage] = useState(0);
  const [order, setOrder] = useState([] as IOrder);
  const [addressHistory, setAddressHistory] = useState<Awaited<ReturnType<typeof getAddresses>>>([]);
  const [deliveryDate, setDeliveryDate] = useState<Date | undefined>(undefined);

  useEffect(() => {
    getDeliveryDate().then(setDeliveryDate).catch(console.error);
    getAddresses().then(setAddressHistory).catch(console.error);
  }, []);

  useEffect(() => window.scrollTo(0, 0), [page]);

  return (
    <div>
      <Main
        visible={page === 0}
        deliveryDate={deliveryDate}
        onAccept={(order: IOrder) => {
          if (page !== 0) {
            return;
          }
          setOrder(order);
          setPage(1);
        }}
      ></Main>
      <Cart
        visible={page === 1}
        deliveryDate={deliveryDate}
        addressHistory={addressHistory}
        order={order}
        onChange={() => setPage(0)}
        onAccept={async (area: string, address: string, comment: string) => {
          if (page !== 1) {
            return;
          }

          const exportedOrder: IExportedOrder = order.map((product) => {
            const size1 = product.exportIn === 'шт' ? `${product.value}шт` : undefined;
            const size2 = product.orderBy[1] !== 'шт' ? `~${sizeByValue(product.value, product)}` : undefined;

            return {
              name: product.name,
              value: getExportValue(product.value, product),
              price: priceByValue(product.value, product),
              units: [size1, size2]
            };
          });
          createPurchase({ items: exportedOrder, area, address, comment });
          window.TWA.close();
        }}
      ></Cart>
    </div>
  );
}
