type IWeightUnit = 'г' | 'кг';
type IVolumeUnit = 'мл' | 'л';
type IPieceUnit = 'шт';
type IMeasurableUnit = IWeightUnit | IVolumeUnit;

export type ISize = {
  min: number;
  max: number;
  unit: IMeasurableUnit;
  isApproximate: boolean;
};

type IPrice<T> = {
  value: number;
  unit: T;
  display: string;
};

type IBaseProduct<T> = {
  name: string;
  price: IPrice<T>; // Цена для отображения, например "600₽/кг"
  image: string; // Ссылка на картинку
  orderBy: [number, T]; // С каким шагом и в каких единицах можно заказывать
  exportIn: T | IPieceUnit;
  consistsOf: string; // Состав продукта
  approximateSize?: ISize; // Для отображения веса

  unavailable?: boolean; // Пометка для недоступных на данный момент товаров
  promo?: {
    name: string; // Название акции - например "15% акция", или "Сегодня за 100"
    price: string; // Скидочная цена, например "510₽/кг"
  };
};

export type IProduct = IBaseProduct<IWeightUnit> | IBaseProduct<IVolumeUnit> | IBaseProduct<IPieceUnit>;

