function price<T>(v: number, multiplier: number, unit: T, short: boolean = false): IPrice<T> {
  const suffix = `/${multiplier === 1 ? '' : multiplier}${unit}`;
  const display = `${v}₽` + (short ? '' : suffix);
  return {
    value: v / multiplier,
    unit,
    display
  };
}

function convert<T>(value: number, from: T, to: T): number {
  if (from === 'кг' && to === 'г') {
    return value * 1000;
  }
  if (from === 'г' && to === 'кг') {
    return value / 1000;
  }
  if (from === 'л' && to === 'мл') {
    return value * 1000;
  }
  if (from === 'мл' && to === 'л') {
    return value / 1000;
  }
  return value;
}

export function priceByValue(value: number, product: IProduct): number {
  const from = product.orderBy[1];
  const to = product.price.unit;
  const converted = convert(value * product.orderBy[0], from, to);
  const result = Math.round(converted * product.price.value * 100) / 100;
  return result;
}

export function approximateSize(size: ISize): string {
  const value = size.min === size.max ? `${size.min}` : `${size.min}-${size.max}`;
  const approximate = size.isApproximate ? '~ ' : '';
  return `(${approximate}${value}${size.unit})`;
}

export function sizeByValue(value: number, product: IProduct): string {
  const result = Math.round(value * product.orderBy[0] * 100) / 100;
  return `${result}${product.orderBy[1]}`;
}

export function getExportValue(value: number, product: IProduct): number {
  if (product.exportIn === 'шт') {
    return value;
  }

  const from = product.orderBy[1];
  const to = product.exportIn;
  const result = Math.round(convert(value * product.orderBy[0], from, to) * 100) / 100;
  return result;
}
