import React, { useCallback, useEffect, useState } from 'react';
import './App.css';
import { Button, Divider, Drawer, Menu, Navbar } from 'react-daisyui';
import { Card } from './Card';
import { IProduct, priceByValue, productList1, productList2 } from './products';

export type IPosition = IProduct & { value: number };
export type IOrder = IPosition[];

function makeOrder(products: { [k: string]: IPosition }): IOrder {
  return Object.values(products).filter(({ value }) => value > 0);
}

function getDayOfWeek(date: Date): string {
  const day = date.getDay();
  const values = ['воскресенье', 'понедельник', 'вторник', 'среда', 'четверг', 'пятница', 'суббота', 'воскресенье'];
  return values[day];
}

function formatDate(date?: Date): string {
  if (!date) {
    return 'неизвестно';
  }
  return `${date.toLocaleDateString('ru-RU')} (${getDayOfWeek(date)})`;
}

interface ITabParams {
  data: IProduct[];
  visible: boolean;
  onChange?: (name: string, value: number) => void;
}

function Tab(params: ITabParams) {
  return (
    <div className={`m-2 grid gap-2 grid-cols-2 sm:grid-cols-3 md:grid-cols-4 ${params.visible ? '' : 'hidden'}`}>
      {params.data.map((product) => (
        <Card key={product.name} product={product} onChange={(value) => params.onChange?.(product.name, value)}></Card>
      ))}
    </div>
  );
}

interface IMainProps {
  visible: boolean;
  deliveryDate?: Date;
  onAccept: (order: IOrder) => void;
}

export function Main(props: IMainProps) {
  const [positions, setPositions] = useState(Object.fromEntries([productList1, productList2].flat().map((value) => [value.name, { ...value, value: 0 }])));
  const [order, setOrder] = useState([] as IOrder);
  const [activeTab, setActiveTab] = useState(0);
  const [activeMenu, setActiveMenu] = useState(0);
  const [mainButtonVisible, setMainButtonVisible] = useState(false);
  const [price, setPrice] = useState(0);
  const [isDrawerVisible, setDrawerVisible] = useState(false);

  const toggleDrawerVisible = useCallback(() => {
    setDrawerVisible((visible) => !visible);
  }, []);

  useEffect(() => setMainButtonVisible(order.length > 0 && !isDrawerVisible && activeMenu === 0), [order, isDrawerVisible, activeMenu]);
  useEffect(() => window.scrollTo(0, 0), [activeTab]);

  const tabCallback = useCallback(
    (name: string, value: number) => {
      positions[name].value = value;
      setPositions(positions);
      const order = makeOrder(positions);
      setPrice(order.map((p) => priceByValue(p.value, p)).reduce((acc, v) => acc + v, 0));
      setOrder(order);
    },
    [positions]
  );

  return (
    <div className={`${props.visible ? '' : 'hidden'} ${mainButtonVisible ? 'mb-32' : 'mb-20'} mt-16 pt-1`}>
      <div className={activeMenu === 0 ? '' : 'hidden'}>
        {[productList1, productList2].map((value, i) => (
          <Tab key={i} data={value} visible={i === activeTab} onChange={tabCallback}></Tab>
        ))}
        <div
          id="bottom-nav"
          className={`flex w-full fixed justify-center ${
            mainButtonVisible ? 'bottom-12' : 'bottom-0'
          } bg-base-100 shadow-[0_-4px_6px_-1px_rgba(0,0,0,0.1),0_-2px_4px_-2px_rgba(0,0,0,0.1)]`}
        >
          <div className={`flex justify-center self-center m-2 ${isDrawerVisible ? 'hidden' : ''}`}>
            <Button className="w-32" color={activeTab === 0 ? 'primary' : undefined} onClick={() => setActiveTab(0)}>
              Деликатесы
            </Button>
            <Divider horizontal={true}></Divider>
            <Button className="w-32" color={activeTab === 1 ? 'primary' : undefined} onClick={() => setActiveTab(1)}>
              Консервы
            </Button>
          </div>
        </div>
      </div>

      {activeMenu === 1 &&
        <div className={`m-2 grid gap-2 grid-cols-1 sm:grid-cols-2 md:grid-cols-3`}>
          <video src="https://batischevpavel.ru/botmovies/IMG_5191.MOV" controls={true} />
          <video src="https://batischevpavel.ru/botmovies/IMG_5192.MOV" controls={true} />
          <video src="https://batischevpavel.ru/botmovies/IMG_5194.MOV" controls={true} />
          <video src="https://batischevpavel.ru/botmovies/IMG_5314.MOV" controls={true} />
          <video src="https://batischevpavel.ru/botmovies/IMG_5364.MOV" controls={true} />
          <video src="https://batischevpavel.ru/botmovies/video_2025-05-30_22-51-39.mp4" controls={true} />
        </div>}

      <Navbar className="z-1 fixed top-0 bg-base-100 shadow-md">
        <Navbar.Start>
          <div className="flex-none">
            <Button shape="square" color="ghost" onClick={toggleDrawerVisible}>
              <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" className="inline-block w-5 h-5 stroke-current">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
              </svg>
            </Button>
          </div>
        </Navbar.Start>
        <Navbar.Center>
          <div className="mr-1">Заказ на</div>
          <b>{formatDate(props.deliveryDate)}</b>
        </Navbar.Center>
        <Navbar.End></Navbar.End>
      </Navbar>

      <Drawer
        open={isDrawerVisible}
        onClickOverlay={toggleDrawerVisible}
        side={
          <Menu size="lg" className="p-4 w-80 h-full bg-base-200 text-base-content pt-20">
            <Menu.Item
              onClick={() => {
                setActiveMenu(0);
                setDrawerVisible(false);
              }}
            >
              <a className={activeMenu === 0 ? 'active' : ''}>Заказ</a>
            </Menu.Item>
            <Menu.Item
              className="mt-4"
              onClick={() => {
                setActiveMenu(1);
                setDrawerVisible(false);
              }}
            >
              <a className={activeMenu === 1 ? 'active' : ''}>Наше производство</a>
            </Menu.Item>
          </Menu>
        }
      ></Drawer>

      {mainButtonVisible && (
        <Button onClick={() => props.onAccept(order)} color="primary" className={`main-button btn-block rounded-none flex bottom-0 fixed`}>
          <span>Корзина</span>
          <span className="w-2"></span>
          <span>{price}₽</span>
        </Button>
      )}
    </div>
  );
}
