const API_ENDPOINT = process.env.REACT_APP_API_ENDPOINT;

export type IExportedOrder = {
  name: string;
  value: number;
  price: number;
  units: (string | undefined)[];
}[];

async function callApi(name: string, data?: any, params?: { method: 'GET' | 'POST' }) {
  const initData = process.env.REACT_APP_IS_DEBUG === "true"
    ? JSON.parse(process.env.REACT_APP_INIT_DATA as string)
    : {
        initData: (window as any)?.Telegram?.WebApp?.initData,
        initDataUnsafe: (window as any)?.Telegram?.WebApp?.initDataUnsafe
      };

  const method = params?.method || 'POST';
  const body = method === 'POST' ? JSON.stringify({ ...initData, ...(data || {}) }) : undefined;

  const response = await fetch(`${API_ENDPOINT}${name}`, {
    method,
    cache: 'no-cache',
    headers: {
      'Content-Type': 'application/json'
    },
    body
  });

  return await response.json();
}

export async function getDeliveryDate(): Promise<Date> {
  const { result } = await callApi('/api/delivery-date', undefined, { method: 'GET' });
  if (typeof result !== 'number') {
    throw new Error('wrong return');
  }
  return new Date(result);
}

export async function getAddresses(): Promise<{ id: number; address: string }[]> {
  const { result } = await callApi('/api/twa/address/read');
  return result;
}

export async function deleteAddress(data: { id: number }) {
  return callApi('/api/twa/address/delete', data);
}

export async function createPurchase(data: { items: IExportedOrder; area: string; address: string; comment?: string }) {
  return callApi('/api/twa/purchase/create', data);
}
