export class TelegramButton {
  button: HTMLInputElement;
  root: HTMLDivElement;
  listeners: number = 0;
  isVisible: boolean = false;

  private height = 48;

  constructor() {
    this.root = document.getElementById('root') as HTMLDivElement;
    this.button = document.createElement('input');
    this.button.className = 'btn btn-primary';
    this.button.type = 'button';
    this.button.value = '';
    this.button.hidden = true;
    this.button.setAttribute(
      'style',
      'z-index: 1; position: fixed; left: 0; right: 0; bottom: 0; height: 40px; border-top-left-radius: 0; border-top-right-radius: 0'
    );
    document.getElementsByTagName('body').item(0)?.appendChild(this.button);
  }

  onClick(handler: () => void) {
    this.listeners += 1;
    this.button.addEventListener('click', handler);
  }

  offClick(handler: () => void) {
    this.listeners -= 1;
    this.button.removeEventListener('click', handler);
  }

  show() {
    this.button.hidden = false;
    this.isVisible = true;
    this.root.setAttribute('style', `margin-bottom: ${this.height}px; overflow: hidden;`);
    (document.getElementById('bottom-nav') as HTMLDivElement).setAttribute('style', `bottom: ${this.height}px`);
  }

  hide() {
    this.button.hidden = true;
    this.isVisible = false;
    this.root.setAttribute('style', 'margin-bottom: 0px; overflow: hidden;');
    (document.getElementById('bottom-nav') as HTMLDivElement).setAttribute('style', 'bottom: 0px');
  }

  setText(text: string) {
    this.button.value = text;
  }
}
