import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import { App } from './App';
import reportWebVitals from './reportWebVitals';
import { Theme } from 'react-daisyui';
import { TelegramButton } from './TelegramButton';

declare global {
  interface Window {
    TWA: any;
  }
}

window.TWA = (window as any)?.Telegram?.WebApp;
if (process.env.REACT_APP_IS_DEBUG === 'true' || window.TWA.initData.length <= 0) {
  document.documentElement.style.setProperty('--tg-theme-button-color', '#50a8eb');
  document.documentElement.style.setProperty('--tg-theme-button-text-color', '#ffffff');
  document.documentElement.style.setProperty('--tg-theme-bg-color', '#ffffff');
  document.documentElement.style.setProperty('--tg-theme-text-color', '#000000');
  window.TWA = { ...window.TWA, MainButton: new TelegramButton() };
}

const theme = window.TWA.colorScheme;
const root = ReactDOM.createRoot(document.getElementById('root') as HTMLElement);

root.render(
  <React.StrictMode>
    <Theme dataTheme={theme}>
      <App />
    </Theme>
  </React.StrictMode>
);

window.TWA.ready?.();
window.TWA.expand?.();

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
