import { Button, Input, Join, Mask, Select, Table } from 'react-daisyui';
import { IOrder, IPosition } from './Main';
import { useEffect, useState } from 'react';
import { sizeByValue, priceByValue } from './products';
import { deleteAddress } from './api';

function formatDate(date?: Date): string {
  if (!date) {
    return 'неизвестно';
  }
  return `${date.toLocaleDateString('ru-RU')}`;
}

function Row(params: IPosition) {
  return (
    <tr key={params.name}>
      <td>
        <Mask variant="squircle" src={params.image} className="size-10" />
      </td>
      <td style={{ wordBreak: 'break-all' }}>
        <div>{params.name}</div>
      </td>
      <td className="text-center">
        <div className="whitespace-nowrap">{params.exportIn === 'шт' && <div>{params.value}шт</div>}</div>
        <div className="whitespace-nowrap">{params.orderBy[1] !== 'шт' && <div>{`~${sizeByValue(params.value, params)}`}</div>}</div>
      </td>
      <td>
        <div className="whitespace-nowrap text-right">{`${priceByValue(params.value, params)}₽`}</div>
      </td>
    </tr>
  );
}

interface IAddressButtonParam {
  id: number;
  address: string;
  visible?: boolean;
  onClick?: () => void;
}

function AddressButton(params: IAddressButtonParam) {
  const [innerVisible, setInnerVisible] = useState(true);
  const visible = (typeof params.visible === 'boolean' ? params.visible : true) && innerVisible;

  return (
    <Join vertical={false} className={`flex w-full max-w-full ${visible ? '' : 'hidden'}`}>
      <Button color="primary" key={params.address} className="flex-1 truncate join-item" onClick={params?.onClick}>
        <div className="w-full truncate">{params.address}</div>
      </Button>
      <Button
        className="join-item font-bold flex-none w-12"
        color="primary"
        onClick={() => {
          setInnerVisible(false);
          deleteAddress({ id: params.id });
        }}
      >
        ✕
      </Button>
    </Join>
  );
}

export interface ICartProps {
  visible: boolean;
  order: IOrder;
  deliveryDate?: Date;
  addressHistory: { id: number; address: string }[];
  onChange?: () => void;
  onAccept?: (area: string, address: string, comment: string) => void;
}

export function Cart(props: ICartProps) {
  const [area, setArea] = useState('Район');
  const [address, setAddress] = useState('');
  const [comment, setComment] = useState('');

  const [addressPlaceholder, setAddressPlaceholder] = useState('Сначала выберите район доставки');

  const [isAddressActive, setIsAddrsessActive] = useState(false);
  const [isAddressVisible, setIsAddressVisible] = useState(true);

  const [mainButtonEnabled, setMainButtonEnabled] = useState(false);
  const [mainButtonClicked, setMainButtonClicked] = useState(false);
  const price = props.order.map((p) => priceByValue(p.value, p)).reduce((acc, v) => acc + v, 0);

  useEffect(() => {
    setMainButtonEnabled(address.length > 0 && area.length > 0);
  }, [area, address]);

  return (
    <div>
      <div className={`${props.visible ? '' : 'hidden'}`}>
        <div className="m-4 mb-16">
          <div className="flex w-full">
            <div className="grow text-nowrap self-center font-bold text-xl">Ваш заказ:</div>
            <Button variant="outline" color="success" size="sm" onClick={() => props?.onChange?.()}>
              Изменить
            </Button>
          </div>
          <div className="overflow-x-auto">
            <Table className="cart-table rounded-box" size="md">
              <Table.Body>
                {props.order.map(Row)}
                <tr>
                  <td colSpan={4}>
                    <div className="flex justify-between">
                      <div>Примерная сумма заказа*:</div>
                      <div>{price}₽</div>
                    </div>
                  </td>
                </tr>
                <tr>
                  <td colSpan={4} className="grow text-right">
                    <div className="flex justify-between">
                      <div>Дата:</div>
                      <div>{formatDate(props.deliveryDate)}</div>
                    </div>
                  </td>
                </tr>
              </Table.Body>
            </Table>
            <Table className="rounded-box" size="md">
              <Table.Body></Table.Body>
            </Table>
          </div>

          <Input
            placeholder="Комментарий к заказу"
            className="w-full text-base"
            value={comment}
            onKeyDown={(e) => {
              if (e.key === 'Enter') {
                e.preventDefault();
                (e.target as HTMLInputElement).blur();
              }
            }}
            onChange={(e) => setComment(e.target.value)}
          />

          <Select
            className="w-full mt-2"
            onChange={(e) => {
              const area = e.target.value;
              setArea(area);
              if (area === 'Самовывоз') {
                setIsAddressVisible(false);
                setAddress(area);
                setAddressPlaceholder('');
                setIsAddrsessActive(false);
              } else {
                setIsAddressVisible(true);
                setAddress('');
                setAddressPlaceholder('Введите адрес доставки');
                setIsAddrsessActive(true);
              }
            }}
            value={area}
          >
            <option disabled={true}>Район</option>
            <option>Заволжский</option>
            <option>Центр</option>
            <option>Самовывоз</option>
          </Select>

          <Join vertical={false} className={`mt-2 flex w-full max-w-full ${isAddressVisible ? '' : 'hidden'}`}>
            <Input
              placeholder={addressPlaceholder}
              disabled={!isAddressActive}
              className={`w-full text-base ${address.length > 0 ? 'join-item' : ''}`}
              value={address}
              onKeyDown={(e) => {
                if (e.key === 'Enter') {
                  e.preventDefault();
                  (e.target as HTMLInputElement).blur();
                }
              }}
              onChange={(e) => setAddress(e.target.value)}
            ></Input>
            <Button
              className={`join-item font-bold flex-none w-12 ${address.length > 0 ? '' : 'hidden'}`}
              style={{ backgroundColor: 'white', color: '#d8d8d8', borderColor: 'var(--fallback-bc,oklch(var(--bc)/0.2))' }}
              color="primary"
              onClick={() => setAddress('')}
            >
              ✕
            </Button>
          </Join>

          <div className="w-full grid grid-cols-1 gap-2 mt-2 overflow-hidden">
            {props.addressHistory?.map((address) => (
              <AddressButton key={address.id} id={address.id} address={address.address} visible={isAddressActive} onClick={() => setAddress(address.address)} />
            ))}
          </div>

          <div className="pt-4 text-sm text-justify">
            * Приблизительная сумма вашего заказа составляет {price}₽. Конечная цена будет определяться по фактическому весу продукции.
          </div>

          <div className="pt-4 text-sm text-justify">
            Если у вас не получается сделать заказ (технические проблемы или сложности), пожалуйста, опишите Вашу проблему боту и мы с Вами свяжемся.
          </div>
        </div>
        <Button
          onClick={() => {
            setMainButtonClicked(true);
            props?.onAccept?.(area, address, comment);
          }}
          disabled={mainButtonClicked || !mainButtonEnabled}
          color="primary"
          className={`main-button btn-block rounded-none flex bottom-0 fixed`}
        >
          {mainButtonClicked ? 'Отправляем заказ...' : 'Заказать'}
        </Button>
      </div>
    </div>
  );
}
